.privacy-container {
	display: block;
  width: 60%;
  margin: 3rem auto;
	color: #ffffff;
	text-align: center;
}

.privacy-heading {
	margin-bottom: 3rem;
	font-size: 2rem;
}

.privacy-sub-heading {
	font-size: 1.25rem;
	text-indent: 0;
}

.privacy-text {
	font-size: 1.125rem;
	text-align: justify;
	text-indent: 4.5rem;
}

.privacy-summary {
	width: max-content;
	max-width: 100%;
	margin: 0 auto;
	text-indent: 0;
}

@media screen and (max-width: 700px) {

  .privacy-container {
    margin: 1.5rem auto;
  }

  .privacy-heading {
		margin-bottom: 1.5rem;
		font-size: 1rem;
	}

	.privacy-sub-heading {
	  font-size: 0.833rem;
	}

  .privacy-text {
    font-size: 0.75rem;
		text-indent: 1.5rem;
  }

}
