.about-container {
	display: block;
  width: 60%;
  margin: 3rem auto;
	color: #ffffff;
	text-align: center;
}

.about-heading {
	margin-bottom: 3rem;
	font-size: 2rem;
}

.about-text {
	font-size: 1.125rem;
	text-align: justify;
  text-indent: 4.5rem;
}

.about-portfolio-link {
  margin-top: 8%;
}

.portfolio-link {
	color: #e0ae77;
	background-color: #3a444f;
	border: none;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.portfolio-link:hover {
		color: #00ffff;
	}
	
	.portfolio-link:hover::after {
		display: inline-block;
		width: 0.75rem;
		height: 0.75rem;
		content: url(../../images/arrow.svg);
		animation-name: rightArrow;
		animation-duration: 0.4s;
		animation-timing-function: ease-in-out;
		animation-direction: alternate;
		animation-iteration-count: infinite;
	}

}

.touch-link {
	color: #00ffff;
}

@keyframes rightArrow {
  0% {
    margin-left: 0.1rem;
  }
  100% {
    margin-left: 0.5rem;
  }
}

@media screen and (max-width: 700px) {

	.about-heading {
		margin-bottom: 1.5rem;
		font-size: 1rem;
	}

	.about-text {
		font-size: 0.75rem;
		text-indent: 1.5rem;
	}

	.portfolio-link {
		font-size: 0.75rem;
  }

	@media(hover: hover) {

		.portfolio-link:hover::after { 
			width: 0.5rem;
			height: 0.5rem;
		}

	}
	
}
