html {
	font-size: 16px;
}

body {
	margin: 0px;
	background-color: #3a444f;
	font-family: 'Arial', sans-serif;
	text-align: justify;
	line-height: 2.5rem;
}

#parent-div {
	display:flex; 
	max-width: 100%;
	min-height: 100vh;
  flex-direction: column;
}

main {
	display: flex;
	flex-grow: 1;
	align-items: center;
}

header {
	display: block;
	margin-bottom: auto;
	padding: 1.5rem;	
	background-color: #3a444f;
	border-bottom-left-radius: 1rem;
	border-bottom-right-radius: 1rem;
}

.header-nav {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
}

.header-sub-nav {
	display: flex;
	width: 30rem;
	justify-content: space-evenly;
	align-items: center;
}

.header-current-link {
	color: #00ffff;
	background-color: #3a444f;
	border: none;
	font-size: 2rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.header-link {
	color: #e0ae77;
	background-color: #3a444f;
	border: none;
	font-size: 1.125rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

footer {
	display: flex;	
	margin-top: auto;
	padding: 0.3rem;
	color: #e0ae77;
	background-color: #3a444f;
	border-top-left-radius: 1rem;
	border-top-right-radius: 1rem;
	font-size: 0.9375rem;
	font-weight: bold;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
}

.footer-text {
	margin-left: 1rem;
	margin-right: 1rem;
}

.footer-nav {
	display: flex;
	width: 15rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
	justify-content: space-evenly;
	align-items: center;
}

.footer-link {
	color: #e0ae77;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.header-link:hover {
		color: #00ffff;
	}

	.footer-link:hover {
		color: #00ffff;
	}

}

.touch-link {
	color: #00ffff;
}

@media screen and (max-width: 700px) {

	body {
		line-height: 1.25rem;
	}

	header {
		padding: 0.75rem;
	}

	.header-sub-nav {
		width: max-content;
	}

	.header-current-link {
		font-size: 1rem;
		text-transform: uppercase;
	}

	.header-link {	
		font-size: 1rem;
	}

	footer {
		padding: 0.15rem;
		font-size: 0.75rem;
	}

	.footer-text {
		margin-left: 0.5rem;
		margin-right: 0.5rem;
	}

	.footer-nav {
		width: 10rem;
		margin-top: 0.5rem;
		margin-bottom: 0.5rem;
	}

	.footer-link {
		font-size: 0.75rem;
	}

}

@media screen and (max-width: 360px) {

	.header-link {	
		font-size: 0.75rem;
	}

}
